<template>
	<div class="album">
		<similarHeader title='相册' @goBack='goBack' :havaMore='false'></similarHeader>
		<div class="albumTab">
			<van-tabs v-model="active" swipeable title-active-color='#14b16b' title-inactive-color='#646464' sticky>
				<van-tab title="朋友圈相册">
				  <p class='myCircle'>我的朋友圈></p>
				</van-tab>
				<van-tab title="视频动态">
					<p class='noVideo'>暂无视频动态</p>
				</van-tab>
			</van-tabs>
		</div>
	</div>
</template>
<script>
	import  similarHeader from './similar-header'
	export default{
		data(){
			return{
				active:0
			}
		},
		methods:{
			goBack(){
				this.active=0;	
				this.$emit('isshowAlbum',false)
			}
		},
		components:{
			similarHeader
		}
		// beforeRouteLeave (to, from, next) {
		// 	if(to.name==='my'){
		// 		alert(1);
		// 		this.active=0;	
		// 		next();
					
		//     }	     
		//     else{
		//     	next();
		//     }	
		// },
	}
</script>
<style>
	.album{
		background: #fff;
	}
	.van-tabs__line {
		background-color: #18ab6c;
		width: 50vw !important;
		/*transform: translateX(50%) translateX(-50%) !important;*/
	}
	.van-tab span {
		font-size: .34rem;
	}
	.van-tab__pane{
		height: 87vh;
		overflow: hidden;
	}
	.albumTab .myCircle{
		font-size: .32rem;
		color:#556676;
		text-align: right;
		margin: .5rem .5rem 0 0;
	}
	.noVideo{
		font-size: .38rem;
	    position: fixed;
	    left: 50%;
	    top: 50%;
	    color: #666;
	    transform: translate(-50%, -50%);
	}
	.album >>> .van-tabs__line {
		
	}
</style>


